<script>
  import Footer from '@/components/footer'
  export default {
    name: "Dingdan",
    components: {
      'p-footer': Footer
    },
    data () {
      return {

        value1: '',
        Ip_Json: [
          {
          id: 1,
          name: '',
          price: 50,
          count: 1
        }
        ]
      }
    },
    methods:{
      // totalPrice : function(){
      //   var totalP = 0;
      //   for (var i = 0,len = this.Ip_Json.length;i<len;i++) {
      //     totalP+=this.Ip_Json[i].price*this.Ip_Json[i].count;
      //   }
      //   return totalP;
      // },
      tiaocontrct(id){
        this.$router.push({name:'contrct',params:{id:id}})
      }


    }
  }
</script>

<style scoped>

</style>



